
.y-radio.button{font-size:0
}
.y-radio-group .y-radio{position:relative
}
.y-radio-group .y-radio .y-btn{border-radius:0;border-color:#b7b7b7;color:#4c4c4c
}
.y-radio-group .y-radio:hover .y-btn{color:#47b8e0
}
.y-radio-group .y-radio.checked{z-index:1
}
.y-radio-group .y-radio.checked .y-btn{border-color:#47b8e0;color:#47b8e0
}
.y-radio-group .y-radio.disabled .y-btn{cursor:not-allowed;color:#ccc;border-color:#ccc
}
.y-radio-group .y-radio.disabled .y-btn:active{box-shadow:none
}
.y-radio-group .y-radio:first-child .y-btn{border-radius:4px 0 0 4px
}
.y-radio-group .y-radio:last-child .y-btn{border-radius:0 4px 4px 0
}
.y-radio-group .y-radio:not(:last-child){margin-right:-1px
}


