
.y-alert{position:relative;padding:8px 8px 8px 16px;border-radius:6px;color:#666;font-size:12px;line-height:16px;margin-bottom:10px;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;transition:all .3s ease
}
.y-alert>.alert-icon{vertical-align:middle;font-size:22px;margin-right:12px
}
.y-alert>.alert-c{padding-right:40px
}
.y-alert>.alert-c h1,.y-alert>.alert-c h2,.y-alert>.alert-c h3,.y-alert>.alert-c h4,.y-alert>.alert-c h5{font-size:18px;font-weight:700;margin-bottom:6px;margin-top:4px
}
.y-alert>.alert-c span{color:#808080
}
.y-alert .alert-close{position:absolute;top:50%;right:0;margin-top:-10px;font-size:14px;line-height:20px;cursor:pointer
}
.alert-info{background:#e0f3fa;border:1px #b4e2f2 solid
}
.alert-info .alert-icon{color:#47b8e0
}
.alert-success{background:#d7f4eb;border:1px #afe9d8 solid
}
.alert-success .alert-icon{color:#67D5B5
}
.alert-warning{background:#fff1d2;border:1px #ffe19f solid
}
.alert-warning .alert-icon{color:#ffc952
}
.alert-danger{background:#fdc;border:1px #fb9 solid
}
.alert-danger .alert-icon{color:#f50
}
.alert-op-enter-active,.alert-op-leave-active{opacity:0
}


