
.left-nav{width:15%;min-width:230px;padding:12px 0
}
.left-nav .left-nav-title p.title{padding:22px 0;padding-left:22px;margin:0
}
.left-nav a.router-link-active{color:#47b8e0;background:#eaf8fe;position:relative
}
.left-nav a.router-link-active:before{content:"";position:absolute;right:-1px;top:0;width:2px;height:100%;background:#47b8e0
}
.left-nav ul{margin:0
}
.left-nav ul li{margin:0;padding:0
}
.left-nav ul li ul .title{margin-left:40px;font-size:14px;padding:8px 0;color:#99a9bf
}
.left-nav ul li ul li a{padding-left:52px;font-size:14px
}
.left-nav ul li ul li a .cn{font-size:10px;color:#99a9bf;margin-left:6px
}
.left-nav ul li a{transition:.3s all;display:block;width:100%;padding:12px 32px;color:#666;text-decoration:none;box-sizing:border-box
}
.left-nav ul li a:hover{background:#eaf8fe
}
@media screen and (max-width:768px){
.left-nav{width:100%
}
.left-nav a.router-link-active:before{right:0
}
}


