
.message{position:fixed;top:12px;left:50%
}
.message .message-content{position:relative;right:50%;display:-ms-flexbox;display:flex;border-radius:4px;border:1px #d9d9d9 solid;padding:6px 18px;box-shadow:0 0 20px -5px #ccc;background:#fff
}
.message .message-content i{margin-right:18px;font-size:18px;line-height:22px
}
.message .message-content p{margin:0;line-height:22px
}
.message .message-content.success .message-info{color:#87d068
}
.message .message-content.info .message-info{color:#51b7ec
}
.message .message-content.warning .message-info{color:#fa0
}
.message .message-content.danger .message-info{color:#f50
}
.message{transition:all .5s cubic-bezier(.55, 0, .1, 1)
}
.message-enter,.message-leave-active{transform:translateY(-100%);opacity:0
}


