
.y-checkbox{position:relative
}
.y-checkbox .y-checkbox-box{position:relative
}
.y-checkbox .y-checkbox-box .y-checkbox-dom{transition:.3s all;position:absolute;top:50%;left:0;margin-top:-9px;display:block;border-width:1px;box-shadow:inset 0 0 0 1px rgba(0,0,0,0.2);width:18px;height:18px;border-radius:4px;cursor:pointer;background:#fff
}
.y-checkbox .y-checkbox-box .y-checkbox-dom .ion-checkmark{transition:.3s all;display:block;line-height:18px;text-align:center;font-size:12px;color:#fff;transform:scale(0)
}
.y-checkbox .y-checkbox-box .y-checkbox-dom.checked{box-shadow:none;background:#47b8e0
}
.y-checkbox .y-checkbox-box .y-checkbox-dom.checked .ion-checkmark{transform:scale(1)
}
.y-checkbox .y-checkbox-box .y-checkbox-dom.disabled{box-shadow:inset 0 0 0 1px rgba(0,0,0,0.2);background:#cecece
}
.y-checkbox .y-checkbox-box input{position:relative;top:0;left:0;bottom:0;right:0;z-index:-1;opacity:0
}


