
.y-btn{transition:.3s all;font-size:14px;border-radius:4px;background:transparent;padding:4px 12px;cursor:pointer;text-decoration:none;font-family:"Open Sans","Helvetica Neue",Helvetica,"PingFang SC","Microsoft Yahei","WenQuanYi Micro Hei","微软雅黑",Arial,Verdana,sans-serif !important;outline:none;line-height:1.42857143;text-align:center;white-space:nowrap;vertical-align:middle;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
.y-btn:active{box-shadow:inset 0 3px 5px rgba(0,0,0,0.125)
}
.y-btn.lg{padding:4px 15px 5px;font-size:14px;border-radius:6px
}
.y-btn.sm{padding:1px 7px;font-size:12px;border-radius:4px
}
.y-btn.disabled{cursor:not-allowed;color:#ccc;background-color:#f7f7f7;border-color:#d9d9d9
}
.y-btn.disabled:hover{color:#ccc;background-color:#f7f7f7;border-color:#d9d9d9
}
.y-btn.disabled:active{color:#ccc;background-color:#f7f7f7;border-color:#d9d9d9;box-shadow:none
}
.y-btn.disabled:focus{color:#ccc;background-color:#f7f7f7;border-color:#d9d9d9
}
.y-btn.loading{padding-left:27px
}
.y-btn.loading .ion-load-c{margin-left:-17px;margin-right:4px
}
.y-btn.loading .ion-load-c:before{animation:loading 1s infinite linear
}
@keyframes loading{
0%{transform-origin:50% 50%;transform:rotate(0deg)
}
to{transform-origin:50% 50%;transform:rotate(1turn)
}
}
