
.y-tooltip{display:inline-block
}
.y-tooltips{position:absolute;z-index:999999;will-change:opacity;transition:margin .3s cubic-bezier(.645, .045, .355, 1),transform .3s cubic-bezier(.645, .045, .355, 1),opacity .3s cubic-bezier(.645, .045, .355, 1)
}
.y-tooltips.blue .y-tooltips-box{background:rgba(24,151,217,0.95)
}
.y-tooltips.blue .y-tooltips-box *{color:#fff
}
.y-tooltips.blue .y-tooltips-box a{color:#fff;text-decoration:underline
}
.y-tooltips.blue .y-tooltips-delta{border-bottom:4px solid rgba(23,148,212,0.9)
}
.y-tooltips.dark .y-tooltips-box{background:rgba(64,64,64,0.85);color:#fff
}
.y-tooltips.dark .y-tooltips-delta{border-bottom:4px solid rgba(64,64,64,0.85)
}
.y-tooltips.white .y-tooltips-box{background:#fff;color:#666;border:1px solid #e6e6e6;box-shadow:0 0 20px 0 #ccc
}
.y-tooltips.white .y-tooltips-delta{border-bottom:4px solid #e6e6e6
}
.y-tooltips.white .y-tooltips-delta:before{content:"";position:absolute;display:block;width:0;height:0;border-left:4px solid transparent;border-right:4px solid transparent;border-bottom:4px solid #fff;margin-left:-4px;margin-top:1px
}
.y-tooltips .y-tooltips-box{overflow:hidden;padding:6px 8px;border-radius:4px
}
.y-tooltips .y-tooltips-box.slot{padding:6px 12px
}
.y-tooltips .y-tooltips-delta{position:absolute;display:block;width:0;height:0;border-left:4px solid transparent;border-right:4px solid transparent
}
.y-tooltips.bottom{padding-top:4px;transform:translate3d(-50%, 0, 0)
}
.y-tooltips.bottom .y-tooltips-delta{margin-left:-4px;top:0;left:50%
}
.y-tooltips.bottom-left{padding-top:4px;transform:translate3d(0, 0, 0)
}
.y-tooltips.bottom-left .y-tooltips-delta{margin-left:-4px;top:0;left:15px
}
.y-tooltips.bottom-right{padding-top:4px;transform:translate3d(-100%, 0, 0)
}
.y-tooltips.bottom-right .y-tooltips-delta{margin-left:-5px;top:0;right:15px
}
.y-tooltips.top{padding-bottom:4px;transform:translate3d(-50%, -100%, 0)
}
.y-tooltips.top .y-tooltips-delta{transform:rotate(180deg);margin-left:-4px;bottom:0;left:50%
}
.y-tooltips.top-left{padding-bottom:4px;transform:translate3d(0, -100%, 0)
}
.y-tooltips.top-left .y-tooltips-delta{transform:rotate(180deg);margin-left:-4px;bottom:0;left:15px
}
.y-tooltips.top-right{padding-bottom:4px;transform:translate3d(-100%, -100%, 0)
}
.y-tooltips.top-right .y-tooltips-delta{transform:rotate(180deg);margin-left:-4px;bottom:0;right:15px
}
.y-tooltips.left{padding-left:4px;transform:translate3d(0, -50%, 0)
}
.y-tooltips.left .y-tooltips-delta{transform:rotate(-90deg);margin-top:-2px;top:50%;left:-2px
}
.y-tooltips.left-top{padding-left:4px;transform:translate3d(0, 0, 0)
}
.y-tooltips.left-top .y-tooltips-delta{transform:rotate(-90deg);margin-top:-2px;top:10px;left:-2px
}
.y-tooltips.left-bottom{padding-left:4px;transform:translate3d(0, -100%, 0)
}
.y-tooltips.left-bottom .y-tooltips-delta{transform:rotate(-90deg);margin-top:-2px;bottom:10px;left:-2px
}
.y-tooltips.right{padding-right:4px;transform:translate3d(-100%, -50%, 0)
}
.y-tooltips.right .y-tooltips-delta{transform:rotate(90deg);margin-top:-2px;top:50%;right:-2px
}
.y-tooltips.right-top{padding-right:4px;transform:translate3d(-100%, 0, 0)
}
.y-tooltips.right-top .y-tooltips-delta{transform:rotate(90deg);margin-top:-2px;top:10px;right:-2px
}
.y-tooltips.right-bottom{padding-right:4px;transform:translate3d(-100%, -100%, 0)
}
.y-tooltips.right-bottom .y-tooltips-delta{transform:rotate(90deg);margin-top:-2px;bottom:10px;right:-2px
}
.tooltips-top-enter,.tooltips-top-leave-active{opacity:0;margin-top:8px;transform:translate3d(-50%, -100%, 0) scale3d(.8, .8, 1) !important
}
.tooltips-topleft-enter,.tooltips-topleft-leave-active{opacity:0;margin-top:8px;transform:translate3d(-8%, -100%, 0) scale3d(.8, .8, 1) !important
}
.tooltips-topright-enter,.tooltips-topright-leave-active{opacity:0;margin-top:8px;transform:translate3d(-92%, -100%, 0) scale3d(.8, .8, 1) !important
}
.tooltips-bottom-enter,.tooltips-bottom-leave-active{opacity:0;margin-top:-8px;transform:translate3d(-50%, 0, 0) scale3d(.8, .8, 1) !important
}
.tooltips-bottomleft-enter,.tooltips-bottomleft-leave-active{opacity:0;margin-top:-8px;transform:translate3d(-8%, 0, 0) scale3d(.8, .8, 1) !important
}
.tooltips-bottomright-enter,.tooltips-bottomright-leave-active{opacity:0;margin-top:-8px;transform:translate3d(-92%, 0, 0) scale3d(.8, .8, 1) !important
}
.tooltips-left-enter,.tooltips-left-leave-active{opacity:0;margin-left:-8px;transform:translate3d(0, -50%, 0) scale3d(.8, .8, 1) !important
}
.tooltips-lefttop-enter,.tooltips-lefttop-leave-active{opacity:0;margin-left:-8px;transform:translate3d(0, -8%, 0) scale3d(.8, .8, 1) !important
}
.tooltips-leftbottom-enter,.tooltips-leftbottom-leave-active{opacity:0;margin-left:-8px;transform:translate3d(0, -92%, 0) scale3d(.8, .8, 1) !important
}
.tooltips-right-enter,.tooltips-right-leave-active{opacity:0;margin-left:8px;transform:translate3d(-100%, -50%, 0) scale3d(.8, .8, 1) !important
}
.tooltips-righttop-enter,.tooltips-righttop-leave-active{opacity:0;margin-left:8px;transform:translate3d(-92%, 0, 0) scale3d(.8, .8, 1) !important
}
.tooltips-rightbottom-enter,.tooltips-rightbottom-leave-active{opacity:0;margin-left:8px;transform:translate3d(-92%, -100%, 0) scale3d(.8, .8, 1) !important
}


