
.notification-box{position:fixed;right:20px;top:20px;z-index:999
}
.notification-box .notification{position:relative;width:260px;border:1px #ebebeb solid;background:#fff;border-radius:4px;box-shadow:0 0 40px -5px #ccc;display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;padding:12px;margin-bottom:20px;transition:all .5s cubic-bezier(.55, 0, .1, 1)
}
.notification-box .notification .notification-c{margin-left:22px
}
.notification-box .notification .notification-c *{margin:8px 0
}
.notification-box .notification .notification-c h2{font-size:20px
}
.notification-box .notification .notification-c p{color:#808080
}
.notification-box .notification .notification-del{position:absolute;right:16px;top:14px;cursor:pointer;font-size:18px;color:#808080
}
.notification-box .notification .notification-info{font-size:52px;margin-left:12px
}
.notification-box .notification.notification-bg{padding:4px;border:none
}
.notification-box .notification.notification-bg .notification-del{top:50%;margin-top:-12px
}
.notification-box .notification.notification-bg *{color:#fff !important
}
.notification-box .notification.notification-bg.success{background:rgba(103,213,181,0.9)
}
.notification-box .notification.notification-bg.info{background:rgba(71,184,224,0.9)
}
.notification-box .notification.notification-bg.warning{background:rgba(255,201,82,0.9)
}
.notification-box .notification.notification-bg.danger{background:rgba(255,116,115,0.9)
}
.notification-box .notification.success .notification-info{color:#87d068
}
.notification-box .notification.info .notification-info{color:#51b7ec
}
.notification-box .notification.warning .notification-info{color:#fa0
}
.notification-box .notification.danger .notification-info{color:#f50
}
.notification-list{transition:all .5s cubic-bezier(.55, 0, .1, 1)
}
.notification-list-enter,.notification-list-leave-active{transform:translateX(100%);opacity:0
}
.notification-list-move{transition:transform 1s
}
.notification-list-leave-active{position:absolute
}


