
.y-tag{transition:all .2s;display:inline-block;padding:0 6px;background:#ccc;font-size:12px;line-height:20px;height:22px;border-radius:4px;margin-right:6px;vertical-align:middle;overflow:hidden
}
.y-tag i{font-size:12px;padding:0 2px;cursor:pointer;vertical-align:middle
}
.y-tag:hover{opacity:.75
}
.y-tag-gray{background:#f3f3f3;color:#9a9a9a;border:1px solid #c0c0c0
}
.y-tag-info{background:#e0f3fa;color:#47b8e0;border:1px solid #88d1eb
}
.y-tag-success{background:#f3fcf9;color:#67D5B5;border:1px solid #a3e6d2
}
.y-tag-warning{background:#fff9eb;color:#ffb105;border:1px solid #ffe19f
}
.y-tag-danger{background:#fff3f3;color:#ff7473;border:1px solid #ffc0c0
}
.y-tag-enter-active,.y-tag-leave-active{will-change:opacity,transform
}
.y-tag-enter,.y-tag-leave-active{transform:scale3d(.2, .2, .2);width:0;margin-right:0;padding:0;opacity:0
}


