
.modal-box{background:rgba(255,255,255,0.8);position:fixed;top:0;left:0;width:100%;height:100%;display:-ms-flexbox;display:flex;-ms-flex-pack:center;justify-content:center;-ms-flex-align:center;align-items:center;z-index:991;transition:all .3s ease
}
.modal-content{transition:all .3s ease;padding:0 22px;width:400px;border:1px #ebebeb solid;background:#fff;border-radius:4px;box-shadow:0 0 40px -5px #ccc;box-sizing:border-box
}
.modal-content .m-header{margin-top:14px
}
.modal-content .m-header h1,.modal-content .m-header h2,.modal-content .m-header h3,.modal-content .m-header h4,.modal-content .m-header h5{display:inline-block;font-size:22px;font-weight:700;color:#566270;margin:0
}
.modal-content .m-header .notes{font-size:12px;color:#ababab
}
.modal-content .m-body{margin-top:16px;margin-bottom:26px
}
.modal-content .m-body *{color:#898989
}
.modal-content .m-footer{float:right;margin-bottom:18px
}
.modal-content .m-footer .y-btn{margin-left:12px
}
.mdc-enter{opacity:0
}
.mdc-enter .modal-content{transform:scale(1.2)
}
.mdc-leave-active{opacity:0
}
.mdc-leave-active .modal-content{transform:scale(.8)
}
.mask-transition{transition:all .3s ease
}
.mask-enter,.mask-leave{opacity:0
}


