
.coding-box{width:100%;border:1px solid #e9e9e9;border-radius:6px;margin-bottom:16px;transition:box-shadow .2s
}
.coding-box .coding-content{padding:22px;border-bottom:1px solid #e9e9e9
}
.coding-box .coding-bottom{padding:16px 22px;position:relative
}
.coding-box .coding-bottom .coding-title{height:20px;line-height:20px;position:absolute;top:-10px;left:12px;background:#fff;padding:0 12px
}
.coding-box .coding-bottom .coding-button{cursor:pointer;position:absolute;height:20px;line-height:20px;right:16px;bottom:16px;font-size:20px
}
.coding-box .coding-bottom .coding-button i:before{transition:.3s all
}
.coding-box .coding-bottom .coding-button i.top:before{transform:rotateZ(180deg)
}
.coding-box .coding-code{max-height:400px;overflow:auto;height:auto;transition:.3s all;border-top:1px dashed #e9e9e9
}
.coding-box .coding-code pre{background:#fff
}
.coding-box:hover{box-shadow:0 2px 20px -5px #ccc
}
.code-enter-active{transition:height .3s ease
}
.code-leave-active{transition:height .8s cubic-bezier(1, .5, .8, 1)
}
.code-enter,.code-leave-active{max-height:0 !important
}


